            SearchData: function (data) {

                var vm = this;

                var params = `search=${data}&userType=${GetUserInfo.value.UserType}&articleType=&moduleId=`

                DataService.GetArticleList(params).then(function (response) {
                    if (response.data.length > 0 ||  response.data != null) {
                        var groupedResults = {};

                        response.data.forEach(item => {
                            if (!groupedResults[item.articletype]) {
                                groupedResults[item.articletype] = [];
                            }
                            groupedResults[item.articletype].push(item);
                        });
                        vm.GlobalSearchList = groupedResults;
                        vm.IsGlobalDataShow = true;
                        vm.IsMainDataShow = false;
                        vm.IsDataNotFoundShow = false;
                    } else {

                        vm.GlobalSearchList = {};
                        vm.IsGlobalDataShow = false;
                        vm.IsMainDataShow = false;
                        vm.IsDataNotFoundShow = true;
                    }
                });
            },
            // ClearData: function () {
            //     // window.location.reload();
            // },
            SideView: function () {

                this.ShowManageView = true;
