                }, 1000);
            },
            TruncateStr(str, len) {
                var convertstring = '' + str;
                if (convertstring != null)
                    if (convertstring.length <= len) return str;
                    else return convertstring.substring(0, len) + "...";
            },
            DownloadSingleFile: function (fileUrl, fileName) {

                var vm = this;
                vm.isLoading = true;

                // var FileUrl = 'https://cogniterblob.blob.core.windows.net/cg-rohit-enterprises-122628/thumb/63221/1164366/329a8ff0-fb3c-4a35-85e2-0f7cc9d6d317.jpg';

                var params = `FileUrl=${fileUrl}&ModuleId=`

                DataService.DownloadSingleFile(params).then((response) => {

                    var fileURL = window.URL.createObjectURL(new Blob([response.data]));
                    var fileLink = document.createElement('a');
                    fileLink.href = fileURL;
                    fileLink.setAttribute('download', fileName + ".pdf");
                    document.body.appendChild(fileLink);
                    vm.isLoading = false;
                    fileLink.click();

                })

            },
            embeddedUrl(URL, Type) {

                // Extract the video ID from the YouTube URL
                if (Type == 'UploadVideo') {
                    return URL;
                }
                else {
                    const videoId = this.getYouTubeId(URL);
                    // Construct the embedded URL
                    return `https://www.youtube.com/embed/${videoId}`;
                }


            },
            getYouTubeId(url) {

                // Regex to extract the YouTube video ID
                const regExp = /^.*(youtu.be\/|v\/|e\/|u\/\w+\/|embed\/|v=)([^#\&\?]*).*/;
                const match = url.match(regExp);
                if (match && match[2].length === 11) {
                    return match[2];
                } else {
                    return 'Invalid YouTube URL';
                }
            },

            WizardShow() {

                var vm = this;
                vm.IsWizardShow = true;
            }

