                                                            <div
                                                                class="col-sm-12 col-md-12 float-left col-lg-6 col-xl-6">
                                                                <div class="form-group"><label>Address Line1:<span
                                                                            class="mandatory">*</span></label>
                                                                    <input type="text" class="form-control" />
                                                                </div>
                                                            </div>
                                                            <div class="col-12 mt-3">
                                                                <div class="row flex-row-reverse">
                                                                    <div class="col-lg-12 text-right">
                                                                        <a href="javascript:;" id="aChainTicketSave"
                                                                            @click="ShowPreviousData(3)"
                                                                            class="btn btn-warning float-left"
                                                                            title="Previous"><i
                                                                                class="fa fa-chevron-left pr-2"></i><span>Previous</span></a>

                                                                        <a href="javascript:;" id="aChainTicketSave"
                                                                            @click="SkipData(5)"
                                                                            class="btn btn-secondary mr-2"
                                                                            title="Skip"><i
                                                                                class="fa fa-times pr-2"></i><span>Skip</span></a>

                                                                        <a href="#" title="Save & Add New"
                                                                            id="gobacktolistscreen"
                                                                            class="btn btn-primary save-btn mr-2"
                                                                            value="@Model.TicketChainId"><i
                                                                                class="fa fa-floppy-o pr-2"></i><span>Save
                                                                                & Add New</span></a>
                                                                        <a href="#" title="Save & Next"
                                                                            id="gobacktolistscreen"
                                                                            class="btn btn-primary save-btn"
                                                                            value="@Model.TicketChainId"
                                                                            v-on:click="ShowNextTab(5)"><i
                                                                                class="fa fa-floppy-o pr-2"></i><span>Save
                                                                                &amp; Next</span></a>
                                                                    </div>

