                                </div>
                            </div>
                        </div>

                    </section>

                </div>
            </div>
            <LocationPopup v-if="ShowLocationPopup"></LocationPopup>
        </div>

    </div>
</template>
<script>
import DataService from '../../services/DataService';
import LocationPopup from "../../../src/views/HelpAndGuide/LocationPopup.vue";

export default {
    components: {
        LocationPopup
    },

    created: function () {
        this.GetUserTitlebycompanyId(),
            this.GetTimeZone(),
            this.GetCountry(),
            this.GetTagList(),
            this.GetStutusList()
    },

    data: function () {
        return {

            ProgressCount: 0,
            ShowIconFirst: false,
            ShowIconSecond: false,
            ShowIconThird: false,
            ShowIconFourth: false,
            Shift: false,
            ImageValue: '',
            UserTitleList: [],
            TimeZoneList: [],
            CountryList: [],
            StateList: [],
            CountryId: 0,
            TagList: [],
            ShowLocationPopup: false,
            StatusList:[],
            CompanyId:0,
        };
    },

    methods: {
        GetUserTitlebycompanyId: function () {
            var vm = this;

            DataService.GetUserTitlebycompanyId().then((response) => {
                if (response.data.length > 0) {
                    vm.UserTitleList = response.data;
                }

            });
        },
        GetCountry: function () {
            var vm = this;
            DataService.GetCountryList().then((response) => {

                if (response.data.length > 0) {
                    vm.CountryList = response.data;
                }

            });
        },
        GetStateList: function (CountryId) {

            var vm = this;
            var params = `CountryId=${CountryId}`;
            DataService.GetStateList(params).then((response) => {

                if (response.data.length > 0) {
                    vm.StateList = response.data;

                }
                else if (response.data.length == 0) {
                    vm.StateList = [];
                }

            });
        },
        GetTimeZone: function () {
            var vm = this;

            DataService.GetTimeZoneList().then((response) => {
                if (response.data.length > 0) {
                    vm.TimeZoneList = response.data;
                }

            });
        },
        GetTagList: function () {

            var vm = this;

            DataService.GetTagList().then((response) => {

                if (response.data.length > 0) {
                    vm.TagList = response.data;
                }

            });
        },
        close: function () {

            this.$parent.ShowManageView = false;
            $("#modalManageView").modal("hide");

        },


        ShowNextTab: function (type) {

            if (type == 2) {
                $('li.FirstScreen').find('a').removeClass('active');
                $('li.SecondScreen').find('a').addClass('active');
                $('li.ThirdScreen').find('a').removeClass('active');
                $('li.FourthScreen').find('a').removeClass('active');

                $('div#step1').removeClass('active show');
                $('div#step2').addClass('active show');
                $('div#step3').removeClass('active show');
                $('div#step4').removeClass('active show');
                this.ProgressCount = 25;
                this.ShowIconFirst = true;

            }
            else if (type == 3) {
                $('li.FirstScreen').find('a').removeClass('active');
                $('li.SecondScreen').find('a').removeClass('active');
                $('li.ThirdScreen').find('a').addClass('active');
                $('li.FourthScreen').find('a').removeClass('active');

                $('div#step1').removeClass('active show');
                $('div#step2').removeClass('active show');
                $('div#step3').addClass('active show');
                $('div#step4').removeClass('active show');
                this.ProgressCount = 50;
                this.ShowIconSecond = true;
            }

            else if (type == 4) {
                $('li.FirstScreen').find('a').removeClass('active');
                $('li.SecondScreen').find('a').removeClass('active');
                $('li.ThirdScreen').find('a').removeClass('active');
                $('li.FourthScreen').find('a').addClass('active');

                $('div#step1').removeClass('active show');
                $('div#step2').removeClass('active show');
                $('div#step3').removeClass('active show');
                $('div#step4').addClass('active show');
                this.ProgressCount = 75;
                this.ShowIconThird = true;
            }

            else if (type == 5) {
                this.$parent.ShowManageView = false;
                $("#modalManageView").modal("hide");

                this.ProgressCount = 100;
                this.ShowIconFourth = true;
            }

        },

        ShowPreviousData: function (type) {

            if (type == '1') {
                $('li.FirstScreen').find('a').addClass('active');
                $('li.SecondScreen').find('a').removeClass('active');
                $('li.ThirdScreen').find('a').removeClass('active');
                $('li.FourthScreen').find('a').removeClass('active');

                $('div#step1').addClass('active show');
                $('div#step2').removeClass('active show');
                $('div#step3').removeClass('active show');
                $('div#step4').removeClass('active show');
            }
            else if (type == '2') {
                $('li.FirstScreen').find('a').removeClass('active');
                $('li.SecondScreen').find('a').addClass('active');
                $('li.ThirdScreen').find('a').removeClass('active');
                $('li.FourthScreen').find('a').removeClass('active');

                $('div#step1').removeClass('active show');
                $('div#step2').addClass('active show');
                $('div#step3').removeClass('active show');
                $('div#step4').removeClass('active show');
            }
            else if (type == '3') {
                $('li.FirstScreen').find('a').removeClass('active');
                $('li.SecondScreen').find('a').removeClass('active');
                $('li.ThirdScreen').find('a').addClass('active');
                $('li.FourthScreen').find('a').removeClass('active');

                $('div#step1').removeClass('active show');
                $('div#step2').removeClass('active show');
                $('div#step3').addClass('active show');
                $('div#step4').removeClass('active show');
            }


        },

        SkipData: function (type) {

            if (type == 2) {


                $('li.FirstScreen').find('a').removeClass('active');
                $('li.SecondScreen').find('a').addClass('active');
                $('li.ThirdScreen').find('a').removeClass('active');
                $('li.FourthScreen').find('a').removeClass('active');

                $('div#step1').removeClass('active show');
                $('div#step2').addClass('active show');
                $('div#step3').removeClass('active show');
                $('div#step4').removeClass('active show');


            }
            else if (type == 3) {


                $('li.FirstScreen').find('a').removeClass('active');
                $('li.SecondScreen').find('a').removeClass('active');
                $('li.ThirdScreen').find('a').addClass('active');
                $('li.FourthScreen').find('a').removeClass('active');

                $('div#step1').removeClass('active show');
                $('div#step2').removeClass('active show');
                $('div#step3').addClass('active show');
                $('div#step4').removeClass('active show');

            }

            else if (type == 4) {


                $('li.FirstScreen').find('a').removeClass('active');
                $('li.SecondScreen').find('a').removeClass('active');
                $('li.ThirdScreen').find('a').removeClass('active');
                $('li.FourthScreen').find('a').addClass('active');

                $('div#step1').removeClass('active show');
                $('div#step2').removeClass('active show');
                $('div#step3').removeClass('active show');
                $('div#step4').addClass('active show');

            }

            else if (type == 5) {
                this.$parent.ShowManageView = false;
                $("#modalManageView").modal("hide");

            }

        },

        Emptyvalue: function () {

            document.getElementById("Location").value = "";
            $("#cars").val('0');

        },

        imageChangeFunc: function (th) {

            var ext = $('#flAvatar').val().split('.').pop().toLowerCase();


            if ($.inArray(ext, ['gif', 'png', 'jpg', 'jpeg', 'jpe', 'bmp', 'dib', 'ico', 'tif'])) {
                var aa = $('#flAvatar').val().slice(12);
                $('#aaaa').val(aa);
            }
        },

        clearfile: function () {

            $('#aaaa').val("");
        },

        OpenLocationPopup: function () {
           

            this.ShowLocationPopup = true;
            $("#modalLocationView").modal('show');
            setTimeout(function () {
                $("#modalLocationView").modal('show');

            }, 1000);

        },

        
        GetStutusList: function () {
            var vm = this;
         
            var params = `LangCode=en&StatusType=Location&HasGlobal=true`;
            DataService.GetStutusList(params).then((response) => {
                
                if (response.data.length > 0) {
                    vm.StatusList = response.data;
                }
               

            });
        },

    }


}


</script>

